:root {
  --primary-blue: #2f80ed;
  --white: #ffffff;
  --charcoal: #333333;
  --gray: #e0e0e0;
  --teal: #2d9cdb;
  --light-gray: #f7f8fa;
}

.Issue {
  padding: 2.5rem 1.5rem 1rem;
  background-color: var(--light-gray);
  min-height: 100vh;
  position: relative;
}

.home-link {
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--primary-blue);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
  z-index: 2;
}

.home-link svg {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

.home-link:hover {
  color: var(--teal);
}

.heading {
  font-size: 2rem;
  font-weight: 700;
  margin: 0 0 2rem 5.5rem;
  color: var(--primary-blue);
  text-align: center;
  position: absolute;
  top: 1.2rem;
  left: 0;
  right: 0;
}

.ReportOrTrack {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 3rem;
  max-width: 1200px;
  margin: 6rem auto 0;
  padding: 0 1rem;
}

.TrackYourIssue,
.ReportAnIssue {
  background: var(--white);
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 4px 16px rgba(47, 128, 237, 0.08);
  transition: transform 0.3s ease;
  border: 1px solid var(--gray);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TrackYourIssue:hover,
.ReportAnIssue:hover {
  transform: translateY(-5px);
}

.image-container {
  width: 100%;
  height: 220px;
  margin-bottom: 1.5rem;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  background: var(--light-gray);
}

.img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  transition: transform 0.3s ease;
}

.TrackYourIssue:hover .img,
.ReportAnIssue:hover .img {
  transform: scale(1.05);
}

h4 {
  font-size: 1.4rem;
  color: var(--charcoal);
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  padding-bottom: 0.5rem;
}

h4::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 2px;
  background-color: var(--teal);
}

p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--charcoal);
  text-align: center;
  margin-top: auto;
}

.action-button {
  margin-top: 1.5rem;
  padding: 0.8rem 2rem;
  background-color: var(--primary-blue);
  color: var(--white);
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.action-button:hover {
  background-color: var(--teal);
}

@media (max-width: 768px) {
  .Issue {
    padding: 4rem 1rem 2rem;
  }

  .heading {
    font-size: 1.6rem;
    margin-left: 4rem;
    top: 1.4rem;
  }

  .home-link {
    top: 1rem;
    left: 1rem;
  }
}

@media (max-width: 480px) {
  .heading {
    font-size: 1.4rem;
    margin-left: 3.5rem;
    top: 1.6rem;
  }

  .home-link {
    font-size: 0.9rem;
  }

  .home-link svg {
    width: 16px;
    height: 16px;
  }
}
