:root {
  --primary: #2c3e50;
  --secondary: #3498db;
  --accent: #2980b9;
  --success: #27ae60;
  --warning: #f39c12;
  --danger: #e74c3c;
  --light: #ecf0f1;
  --dark: #34495e;
  --gray: #95a5a6;
  --light-gray: #f8f9fa;
  --card-bg: #ffffff;
  --card-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  --border-radius: 10px;
  --transition: all 0.3s ease;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.admin-home-button {
  display: flex;
  align-items: center;
  gap: 8px;
  background: var(--accent);
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
}

.admin-home-button:hover {
  background: var(--primary);
  transform: translateY(-2px);
}

.admin-home-icon {
  font-size: 1.2rem;
}

.admin-back-button {
  background: var(--light-gray);
  color: var(--dark);
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.admin-back-button:hover {
  background: #e0e6ed;
  transform: translateY(-2px);
}

.admin-dashboard-container {
  max-width: 100vw;
  margin: 0 auto;
  padding: 30px 20px;
}

.admin-main-title {
  font-size: 2.5rem;
  color: var(--primary);
  font-weight: 700;
  margin-bottom: 10px;
  text-align: center;
  width: 100%;
}

.admin-filters-container {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 40px;
  padding: 20px;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
}

.admin-filter-group {
  display: flex;
  flex-direction: column;
  min-width: 250px;
}

.admin-filter-label {
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 8px;
  font-size: 1rem;
}

.admin-filter-select {
  padding: 12px 16px;
  border: 1px solid #e0e6ed;
  border-radius: 6px;
  font-size: 1rem;
  background-color: white;
  color: var(--dark);
  transition: var(--transition);
}

.admin-filter-select:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(41, 128, 185, 0.2);
}

.admin-issues-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

@media (max-width: 1200px) {
  .admin-issues-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .admin-issues-grid {
    grid-template-columns: 1fr;
  }
}

.admin-issue-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  overflow: hidden;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.admin-issue-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

.admin-card-header {
  padding: 20px;
  background: var(--light-gray);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 1px solid #eef2f7;
}

.admin-issue-title {
  font-size: 1.2rem;
  color: var(--primary);
  font-weight: 600;
  margin: 0;
  flex: 1;
}

.admin-card-content {
  padding: 20px;
  flex-grow: 1;
}

.admin-card-meta {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
  font-size: 0.9rem;
}

.admin-category {
  background: rgba(52, 152, 219, 0.1);
  color: var(--accent);
  padding: 5px 12px;
  border-radius: 20px;
  font-weight: 500;
}

.admin-date {
  color: var(--gray);
  display: flex;
  align-items: center;
}

.admin-description {
  color: var(--dark);
  margin-bottom: 15px;
  line-height: 1.6;
}

.admin-reporter {
  color: var(--gray);
  font-size: 0.9rem;
  margin-top: 10px;
}

.admin-card-actions {
  padding: 0 20px 20px;
}

.admin-proceed-button {
  width: 100%;
  background: var(--accent);
  color: white;
  border: none;
  padding: 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: var(--transition);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.admin-proceed-button:hover {
  background: var(--primary);
}

.admin-status {
  padding: 6px 15px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: capitalize;
}

.admin-status-reported {
  background: rgba(52, 152, 219, 0.15);
  color: var(--accent);
}
.admin-status-under-review {
  background: rgba(243, 156, 18, 0.15);
  color: #d35400;
}
.admin-status-in-progress {
  background: rgba(46, 204, 113, 0.15);
  color: #27ae60;
}
.admin-status-resolved {
  background: rgba(39, 174, 96, 0.15);
  color: #219653;
}
.admin-status-rejected {
  background: rgba(231, 76, 60, 0.15);
  color: #c0392b;
}

.admin-issue-detail-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px 20px;
}

.admin-issue-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  background: var(--card-bg);
  padding: 25px;
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
}

.admin-issue-main-title {
  font-size: 1.8rem;
  color: var(--primary);
  font-weight: 700;
  margin: 0;
}

.admin-issue-meta {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
  background: var(--light-gray);
  padding: 20px;
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  flex-wrap: wrap;
}

.admin-meta-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.95rem;
}

.admin-meta-label {
  font-weight: 600;
  color: var(--dark);
}

.admin-meta-value {
  color: var(--gray);
}

.admin-content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 30px;
}

@media (max-width: 992px) {
  .admin-content-grid {
    grid-template-columns: 1fr;
  }
}

.admin-description-card,
.admin-image-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  padding: 25px;
  text-align: justify;
}

.admin-section-title {
  color: var(--primary);
  font-size: 1.3rem;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eef2f7;
  font-weight: 600;
}

.admin-description-content {
  color: var(--dark);
  line-height: 1.7;
  font-size: 1.05rem;
}

.admin-image-container {
  margin-top: 15px;
  text-align: center;
}

.admin-issue-image {
  max-width: 100%;
  max-height: 400px;
  height: auto;
  border-radius: 8px;
  border: 1px solid #e0e6ed;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
  object-fit: contain;
}

.admin-status-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .admin-status-grid {
    grid-template-columns: 1fr;
  }
}

.admin-status-form-card,
.admin-history-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  padding: 25px;
}

.admin-status-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.admin-form-group {
  display: flex;
  flex-direction: column;
}

.admin-form-label {
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 8px;
  font-size: 1rem;
}

.admin-form-select,
.admin-form-textarea {
  padding: 12px 16px;
  border: 1px solid #e0e6ed;
  border-radius: 6px;
  font-size: 1rem;
  background-color: white;
  color: var(--dark);
  transition: var(--transition);
}

.admin-form-select:focus,
.admin-form-textarea:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(41, 128, 185, 0.2);
}

.admin-form-textarea {
  min-height: 120px;
  resize: vertical;
}

.admin-submit-button {
  align-self: flex-start;
  background: var(--success);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: var(--transition);
}

.admin-submit-button:hover {
  background: #219653;
}

.admin-history-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 10px;
}

.admin-update-item {
  padding: 20px 0;
  border-bottom: 1px solid #eef2f7;
}

.admin-update-item:last-child {
  border-bottom: none;
}

.admin-update-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  align-items: center;
}

.admin-update-date {
  color: var(--gray);
  font-size: 0.9rem;
}

.admin-update-comment {
  color: var(--dark);
  line-height: 1.6;
  padding-left: 10px;
  border-left: 3px solid var(--accent);
  margin-top: 10px;
}

.admin-no-history {
  color: var(--gray);
  text-align: center;
  padding: 20px;
}

.admin-comments-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  padding: 25px;
}

.admin-comment-input {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
}

.admin-comment-textarea {
  padding: 15px;
  border: 1px solid #e0e6ed;
  border-radius: 6px;
  min-height: 120px;
  resize: vertical;
  transition: var(--transition);
  font-family: inherit;
  font-size: 1rem;
}

.admin-comment-textarea:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(41, 128, 185, 0.2);
}

.admin-comment-button {
  align-self: flex-end;
  background: var(--accent);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: var(--transition);
}

.admin-comment-button:hover {
  background: var(--primary);
}

.admin-comment-list {
  max-height: 500px;
  overflow-y: auto;
  padding-right: 10px;
}

.admin-comment-item {
  padding: 20px 0;
  border-bottom: 1px solid #eef2f7;
}

.admin-comment-item:last-child {
  border-bottom: none;
}

.admin-comment-content {
  color: var(--dark);
  line-height: 1.7;
  padding: 15px;
  background: var(--light-gray);
  border-radius: 6px;
  margin-bottom: 10px;
}

.admin-comment-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  color: var(--gray);
}

.admin-comment-author {
  font-weight: 600;
}

.admin-no-comments {
  color: var(--gray);
  text-align: center;
  padding: 20px;
}

.admin-loading,
.admin-no-issues {
  text-align: center;
  padding: 40px;
  color: var(--gray);
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  font-size: 1.1rem;
}

.admin-error {
  color: var(--danger);
  padding: 40px;
  text-align: center;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
}

.admin-description-content,
.admin-update-comment,
.admin-comment-content {
  overflow-wrap: break-word;
  word-break: break-word;
  max-width: 100%;
}

.admin-description-card,
.admin-image-card,
.admin-status-form-card,
.admin-history-card,
.admin-comments-card {
  min-height: 0;
  height: auto;
}

.admin-content-grid,
.admin-status-grid {
  align-items: flex-start;
}
