.home-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.home-content {
  flex: 1;
  padding: 2rem 5%;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
}
.heading {
  text-align: center;
}
.home-tagline {
  color: #333333;
  font-weight: 500;
}
@media (max-width: 768px) {
  .home-content {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .home-content {
    padding: 1rem;
  }
}
